export interface Bloque {
  id: string;
  tipo: TipoBloque;
  contenido: Record<string, unknown>;
  estilos: EstilosBloque;
  configuracion: ConfiguracionBloque;
}

export type TipoBloque =
  | "titulo"
  | "subtitulo"
  | "texto"
  | "imagen"
  | "galeria"
  | "video"
  | "card"
  | "banner"
  | "grid"
  | "flex"
  | "accordion"
  | "tabs"
  | "faq"
  | "timeline"
  | "cta"
  | "custom"
  | "separador"
  | "gif"
  | "carrusel"
  | "testimonial"
  | "lista_iconos"
  | "tabla"
  | "cita"
  | "social"
  | "contador"
  | "newsletter"
  | "codigo"
  | "espaciador"
  | "card_texto"
  | "card_imagen"
  | "card_mixta"
  | "card_cta"
  | "card_links"
  | "slider";

export interface EstilosBloque {
  color?: string;
  background?: string;
  padding?: string;
  margin?: string;
  borderRadius?: string;
  shadow?: string;
  textAlign?: "left" | "center" | "right";
  fontSize?: string;
  fontWeight?: string;
  width?: string;
  height?: string;
  objectFit?: "cover" | "contain" | "fill" | "none";
  opacity?: string;
  overlay?: string;
  display?: "block" | "flex" | "grid" | "inline" | "inline-block" | "none";
  gridTemplateColumns?: string;
  gridGap?: string;
  flexDirection?: "row" | "column" | "row-reverse" | "column-reverse";
  flexWrap?: "wrap" | "nowrap" | "wrap-reverse";
  justifyContent?: "flex-start" | "flex-end" | "center" | "space-between" | "space-around" | "space-evenly";
  alignItems?: "stretch" | "flex-start" | "flex-end" | "center" | "baseline";
  gap?: string;
  border?: string;
  animation?: string;
  maxWidth?: string;
  lineHeight?: string;
  letterSpacing?: string;
  textTransform?: "none" | "capitalize" | "uppercase" | "lowercase";
}

export interface ConfiguracionBloque {
  autoplay?: boolean;
  loop?: boolean;
  muted?: boolean;
  controls?: boolean;
  aspectRatio?: string;
  fullWidth?: boolean;
  lazyLoading?: boolean;
  responsive?: {
    mobile?: Partial<EstilosBloque>;
    tablet?: Partial<EstilosBloque>;
    desktop?: Partial<EstilosBloque>;
  };
  columns?: number;
  order?: number;
  html?: string;
  items?: Array<Record<string, unknown>>;
  slides?: Array<Record<string, unknown>>;
  preguntas?: Array<{ pregunta: string; respuesta: string }>;
  eventos?: Array<{ fecha: string; titulo: string; descripcion: string }>;
  testimonios?: Array<{ nombre: string; cargo: string; texto: string; imagen?: string }>;
  iconos?: Array<{ icono: string; texto: string }>;
  filas?: Array<Array<string>>;
  url?: string;
  enlace?: string;
  textoBoton?: string;
  tipoBoton?: "primario" | "secundario" | "outline";
  redes?: Array<{ nombre: string; url: string; icono: string }>;
  valorFinal?: number;
  duracion?: number;
  sufijo?: string;
  prefijo?: string;
  campos?: Array<{ nombre: string; tipo: string; placeholder: string; requerido: boolean }>;
}

export interface Publicacion {
  id: string;
  titulo: string;
  slug: string;
  estado: "borrador" | "publicado";
  fechaCreacion: string;
  fechaModificacion: string;
  bloques: Bloque[];
  meta?: {
    descripcion?: string;
    imagen?: string;
    tags?: string[];
  };
}

export const STORAGE_KEY = "noticias_vet_contenido";

export const generarId = (): string => {
  if (typeof window === "undefined") return `temp-${Date.now()}`;
  return `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
};

export const slugify = (text: string): string => {
  return text
    .toString()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .toLowerCase()
    .trim()
    .replace(/\s+/g, "-")
    .replace(/[^\w-]+/g, "")
    .replace(/--+/g, "-");
};

export const obtenerFechaActual = (): string => {
  return new Date().toISOString();
};

