// /app/noticiasvetchile/page.tsx
"use client";

import React, { useState, useEffect } from "react";
import { Publicacion, STORAGE_KEY } from "@/app/types/noticias";
import { BloqueRenderer } from "./components";

// ==========================================
// PAGE COMPONENT
// ==========================================

const ITEMS_POR_PAGINA = 8;

export default function NoticiasVetChilePage(): JSX.Element {
  const [publicaciones, setPublicaciones] = useState<Publicacion[]>([]);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cargando, setCargando] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // ==========================================
  // CARGA DE DATOS
  // ==========================================

  useEffect(() => {
    if (typeof window === "undefined") {
      setCargando(false);
      return;
    }

    try {
      const dataGuardada = localStorage.getItem(STORAGE_KEY);
      
      if (!dataGuardada) {
        setPublicaciones([]);
        setCargando(false);
        return;
      }

      const parsed = JSON.parse(dataGuardada);
      
      if (!Array.isArray(parsed)) {
        throw new Error("Formato de datos inv치lido");
      }

      // Filtrar solo publicaciones publicadas y ordenar por fecha
      const publicadas = parsed
        .filter((pub: Publicacion) => pub.estado === "publicado")
        .sort((a: Publicacion, b: Publicacion) => 
          new Date(b.fechaModificacion).getTime() - new Date(a.fechaModificacion).getTime()
        );

      setPublicaciones(publicadas);
    } catch (err) {
      console.error("Error al cargar noticias:", err);
      setError("Error al cargar las noticias. Por favor, intenta m치s tarde.");
    } finally {
      setTimeout(() => setCargando(false), 300);
    }
  }, []);

  // ==========================================
  // PAGINACI칍N
  // ==========================================

  const totalPaginas = Math.ceil(publicaciones.length / ITEMS_POR_PAGINA);
  
  const publicacionesPaginaActual = publicaciones.slice(
    (paginaActual - 1) * ITEMS_POR_PAGINA,
    paginaActual * ITEMS_POR_PAGINA
  );

  const cambiarPagina = (nuevaPagina: number) => {
    if (nuevaPagina >= 1 && nuevaPagina <= totalPaginas) {
      setPaginaActual(nuevaPagina);
      window.scrollTo({ top: 0, behavior: "smooth" });
    }
  };

  const irPaginaAnterior = () => {
    if (paginaActual > 1) {
      setPaginaActual(paginaActual - 1);
      window.scrollTo({ top: 0, behavior: "smooth" });
    }
  };

  const irPaginaSiguiente = () => {
    if (paginaActual < totalPaginas) {
      setPaginaActual(paginaActual + 1);
      window.scrollTo({ top: 0, behavior: "smooth" });
    }
  };

  // ==========================================
  // ESTADOS DE CARGA Y ERROR
  // ==========================================

  if (cargando) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <h2 className="text-xl font-semibold text-gray-800 mb-2">Cargando noticias...</h2>
          <p className="text-gray-500">Preparando el contenido para ti</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
        <div className="text-center max-w-md">
          <div className="text-6xl mb-4">丘멆잺</div>
          <h2 className="text-2xl font-bold text-gray-800 mb-4">Algo sali칩 mal</h2>
          <p className="text-red-600 mb-6">{error}</p>
          <button
            onClick={() => window.location.reload()}
            className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition-colors"
          >
            Intentar de nuevo
          </button>
        </div>
      </div>
    );
  }

  if (publicaciones.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
        <div className="text-center max-w-md">
          <div className="text-6xl mb-4">游닗</div>
          <h2 className="text-2xl font-bold text-gray-800 mb-4">No hay noticias a칰n</h2>
          <p className="text-gray-600 mb-6">
            Las publicaciones aparecer치n aqu칤 cuando est칠n disponibles.
          </p>
        </div>
      </div>
    );
  }

  // ==========================================
  // RENDER PRINCIPAL
  // ==========================================

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="bg-white shadow-sm sticky top-0 z-30 border-b border-gray-100">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 h-16 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <h1 className="text-xl sm:text-2xl font-bold text-gray-900">
              Noticias VetChile
            </h1>
          </div>
          <div className="text-sm text-gray-500">
            {publicaciones.length} {publicaciones.length === 1 ? "publicaci칩n" : "publicaciones"}
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Grid de publicaciones - 8 por p치gina */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
          {publicacionesPaginaActual.map((pub) => (
            <article
              key={pub.id}
              className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition-shadow flex flex-col"
            >
              {/* Preview del primer bloque o placeholder */}
              <div className="h-32 sm:h-40 bg-gray-100 flex items-center justify-center">
                {pub.bloques.find(b => b.tipo === 'imagen' || b.tipo === 'banner') ? (
                  <div className="w-full h-full bg-blue-100 flex items-center justify-center text-blue-600">
                    <svg className="w-12 h-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                    </svg>
                  </div>
                ) : (
                  <div className="text-gray-400 text-4xl">游늯</div>
                )}
              </div>

              <div className="p-4 flex-1 flex flex-col">
                <h2 className="font-bold text-gray-900 mb-2 line-clamp-2 text-sm sm:text-base">
                  {pub.titulo || "Sin t칤tulo"}
                </h2>
                
                {pub.meta?.descripcion && (
                  <p className="text-gray-600 text-xs sm:text-sm line-clamp-3 mb-4 flex-1">
                    {pub.meta.descripcion}
                  </p>
                )}

                <div className="flex items-center justify-between text-xs text-gray-500 mt-auto pt-3 border-t border-gray-100">
                  <span>{new Date(pub.fechaModificacion).toLocaleDateString("es-CL")}</span>
                  <span>{pub.bloques.length} bloques</span>
                </div>
              </div>
            </article>
          ))}
        </div>

        {/* Paginaci칩n */}
        {totalPaginas > 1 && (
          <div className="mt-8 sm:mt-12 flex flex-col items-center gap-4">
            <div className="flex items-center gap-2">
              {/* Flecha Anterior */}
              <button
                onClick={irPaginaAnterior}
                disabled={paginaActual === 1}
                className="flex items-center gap-1 px-3 sm:px-4 py-2 rounded-lg border border-gray-300 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm font-medium"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
                <span className="hidden sm:inline">Anterior</span>
              </button>

              {/* N칰meros de p치gina */}
              <div className="flex gap-1 sm:gap-2">
                {Array.from({ length: totalPaginas }, (_, i) => i + 1).map((num) => (
                  <button
                    key={num}
                    onClick={() => cambiarPagina(num)}
                    className={`w-8 h-8 sm:w-10 sm:h-10 rounded-lg text-sm font-medium transition-colors ${
                      paginaActual === num
                        ? "bg-blue-600 text-white"
                        : "border border-gray-300 hover:bg-gray-50 text-gray-700"
                    }`}
                  >
                    {num}
                  </button>
                ))}
              </div>

              {/* Flecha Siguiente */}
              <button
                onClick={irPaginaSiguiente}
                disabled={paginaActual === totalPaginas}
                className="flex items-center gap-1 px-3 sm:px-4 py-2 rounded-lg border border-gray-300 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm font-medium"
              >
                <span className="hidden sm:inline">Siguiente</span>
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </button>
            </div>

            {/* Info de p치gina */}
            <p className="text-sm text-gray-500">
              P치gina <span className="font-medium text-gray-900">{paginaActual}</span> de{" "}
              <span className="font-medium text-gray-900">{totalPaginas}</span>
            </p>
          </div>
        )}
      </div>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-8 mt-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <p className="text-gray-400 text-sm">
            춸 {new Date().getFullYear()} VetChile. Todos los derechos reservados.
          </p>
        </div>
      </footer>
    </div>
  );
}
